import {Col, Dropdown, Icon, Menu, Row} from 'antd';
import React, {Component, Suspense} from 'react';

import {Dispatch} from 'redux';
import {GridContent} from '@ant-design/pro-layout';
import {RadioChangeEvent} from 'antd/es/radio';
import {RangePickerValue} from 'antd/es/date-picker/interface';
import PageLoading from './components/PageLoading';
import {getTimeDistance} from './utils/utils';
import {AnalysisData, VisitDataType} from './data';
import './style.css'

import moment from "moment";

const IntroduceRow = React.lazy(() => import('./components/IntroduceRow'));
const SalesCard = React.lazy(() => import('./components/SalesCard'));
const TopSearch = React.lazy(() => import('./components/TopSearch'));
const ProportionSales = React.lazy(() => import('./components/ProportionSales'));
const OfflineData = React.lazy(() => import('./components/OfflineData'));

interface PAGE_NAME_UPPER_CAMEL_CASEProps {
    BLOCK_NAME_CAMEL_CASE: AnalysisData;
    dispatch: Dispatch<any>;
    loading: boolean;
}

interface PAGE_NAME_UPPER_CAMEL_CASEState {
    salesType: 'all' | 'online' | 'stores';
    currentTabKey: string;
    rangePickerValue: RangePickerValue;
}

class PAGE_NAME_UPPER_CAMEL_CASE extends Component<PAGE_NAME_UPPER_CAMEL_CASEProps,
    PAGE_NAME_UPPER_CAMEL_CASEState> {
    state: PAGE_NAME_UPPER_CAMEL_CASEState = {
        salesType: 'all',
        currentTabKey: '',
        rangePickerValue: getTimeDistance('year'),
    };

    reqRef: number = 0;

    timeoutId: number = 0;

    componentDidMount() {
        /*const {dispatch} = this.props;
        this.reqRef = requestAnimationFrame(() => {
            dispatch({
                type: 'BLOCK_NAME_CAMEL_CASE/fetch',
            });
        });*/
    }

    componentWillUnmount() {
        const {dispatch} = this.props;
       /* dispatch({
            type: 'BLOCK_NAME_CAMEL_CASE/clear',
        });*/
        cancelAnimationFrame(this.reqRef);
        clearTimeout(this.timeoutId);
    }

    handleChangeSalesType = (e: RadioChangeEvent) => {
        this.setState({
            salesType: e.target.value,
        });
    };

    handleTabChange = (key: string) => {
        this.setState({
            currentTabKey: key,
        });
    };

    handleRangePickerChange = (rangePickerValue: RangePickerValue) => {
        const {dispatch} = this.props;
        this.setState({
            rangePickerValue,
        });

      /*  dispatch({
            type: 'BLOCK_NAME_CAMEL_CASE/fetchSalesData',
        });*/
    };

    selectDate = (type: 'today' | 'week' | 'month' | 'year') => {
        const {dispatch} = this.props;
        this.setState({
            rangePickerValue: getTimeDistance(type),
        });

       /* dispatch({
            type: 'BLOCK_NAME_CAMEL_CASE/fetchSalesData',
        });*/
    };

    isActive = (type: 'today' | 'week' | 'month' | 'year') => {
        const {rangePickerValue} = this.state;
        const value = getTimeDistance(type);
        if (!rangePickerValue[0] || !rangePickerValue[1]) {
            return '';
        }
        if (
            rangePickerValue[0].isSame(value[0], 'day') &&
            rangePickerValue[1].isSame(value[1], 'day')
        ) {
            return 'currentDate';
        }
        return '';
    };

    render() {
        const {rangePickerValue, salesType, currentTabKey} = this.state;

        const visitData: VisitDataType[] = [];
        const beginDay = new Date().getTime();

        const fakeY = [7, 5, 4, 2, 4, 0, 20];
        for (let i = 0; i < fakeY.length; i += 1) {
            visitData.push({
                x: moment(new Date(beginDay + 1000 * 60 * 60 * 24 * i)).format('YYYY-MM-DD'),
                y: fakeY[i],
            });
        }

        const visitData2 = [];
        const fakeY2 = [1, 6, 4, 8, 3, 7, 2];
        for (let i = 0; i < fakeY2.length; i += 1) {
            visitData2.push({
                x: moment(new Date(beginDay + 1000 * 60 * 60 * 24 * i)).format('YYYY-MM-DD'),
                y: fakeY2[i],
            });
        }


        let salesData = [];
        for (let i = 0; i < 12; i += 1) {
            salesData.push({
                x: `${i + 1}月`,
                y: Math.floor(Math.random() * 1000) + 200,
            });
        }

        const searchData = [];
        for (let i = 0; i < 50; i += 1) {
            searchData.push({
                index: i + 1,
                keyword: `search keyword-${i}`,
                count: Math.floor(Math.random() * 1000),
                range: Math.floor(Math.random() * 100),
                status: Math.floor((Math.random() * 10) % 2),
            });
        }

        const salesTypeData = [
            {
                x: 'Household appliances',
                y: 4544,
            },
            {
                x: 'Drinking Tea',
                y: 3321,
            },
            {
                x: 'Personal health',
                y: 3113,
            },
            {
                x: 'Drinking',
                y: 2341,
            },
            {
                x: 'health',
                y: 1231,
            },
            {
                x: 'Tea',
                y: 1231,
            },
        ];

        const salesTypeDataOnline = [
            {
                x: 'free',
                y: 244,
            },
            {
                x: 'instantly',
                y: 321,
            },
            {
                x: 'translates',
                y: 311,
            },
            {
                x: 'over',
                y: 41,
            },
            {
                x: 'instantly',
                y: 121,
            },
            {
                x: 'Google',
                y: 111,
            },
        ];

        const salesTypeDataOffline = [
            {
                x: '家用电器',
                y: 99,
            },
            {
                x: '食用酒水',
                y: 188,
            },
            {
                x: '个护健康',
                y: 344,
            },
            {
                x: '服饰箱包',
                y: 255,
            },
            {
                x: '其他',
                y: 65,
            },
        ];

        const offlineData = [];
        for (let i = 0; i < 10; i += 1) {
            offlineData.push({
                name: `Stores ${i}`,
                cvr: Math.ceil(Math.random() * 9) / 10,
            });
        }

        const offlineChartData = [];
        for (let i = 0; i < 20; i += 1) {
            offlineChartData.push({
                x: new Date().getTime() + 1000 * 60 * 30 * i,
                y1: Math.floor(Math.random() * 100) + 10,
                y2: Math.floor(Math.random() * 100) + 10,
            });
        }
        let salesPieData;
        if (salesType === 'all') {
            salesPieData = salesTypeData;
        } else {
            salesPieData = salesType === 'online' ? salesTypeDataOnline : salesTypeDataOffline;
        }
        const menu = (
            <Menu>
                <Menu.Item>1</Menu.Item>
                <Menu.Item>2</Menu.Item>
            </Menu>
        );

        const dropdownGroup = (
            <span className='iconGroup'>
            <Dropdown overlay={menu} placement="bottomRight">
        <Icon type="ellipsis"/>
            </Dropdown>
            </span>
        );

        const activeKey = currentTabKey || (offlineData[0] && offlineData[0].name);
        return (
            <GridContent >
                <React.Fragment>
                    <Suspense fallback={<PageLoading/>}>
                        <IntroduceRow loading={false} visitData={visitData}/>
                    </Suspense>
                    <Suspense fallback={null}>
                        <SalesCard
                            rangePickerValue={rangePickerValue}
                            salesData={salesData}
                            isActive={this.isActive}
                            handleRangePickerChange={this.handleRangePickerChange}
                            loading={false}
                            selectDate={this.selectDate}
                        />
                    </Suspense>
                    <Row
                        gutter={24}
                        type="flex"
                        style={{
                            marginTop: 24,
                        }}
                    >
                        <Col xl={12} lg={24} md={24} sm={24} xs={24}>
                            <Suspense fallback={null}>
                                <TopSearch
                                    loading={false}
                                    visitData2={visitData2}
                                    searchData={searchData}
                                    dropdownGroup={dropdownGroup}
                                />
                            </Suspense>
                        </Col>
                        <Col xl={12} lg={24} md={24} sm={24} xs={24}>
                            <Suspense fallback={null}>
                                <ProportionSales
                                    dropdownGroup={dropdownGroup}
                                    salesType={salesType}
                                    loading={false}
                                    salesPieData={salesPieData}
                                    handleChangeSalesType={this.handleChangeSalesType}
                                />
                            </Suspense>
                        </Col>
                    </Row>

                    <Suspense fallback={null}>
                        <OfflineData
                            activeKey={activeKey}
                            loading={false}
                            offlineData={offlineData}
                            offlineChartData={offlineChartData}
                            handleTabChange={this.handleTabChange}
                        />
                    </Suspense>

                </React.Fragment>
            </GridContent>
        );
    }
}

export default PAGE_NAME_UPPER_CAMEL_CASE;
