import React, {Component, Suspense} from 'react';
import {Redirect} from "react-router-dom";
import {Axis, Chart, Geom, Legend, Tooltip} from 'bizcharts';
import {GridContent} from '@ant-design/pro-layout';
import {Col, Divider, Row} from "antd";
import Pie from "./pie";
import Line from "./line";
import Liquid from "./liquid";
import Container from "reactstrap/es/Container";


export default class Home extends Component {
    data = [
        {genre: 'Resolved', sold: 3},
        {genre: 'Pending', sold: 1},
        {genre: 'New', sold: 2},
    ];

    cols = {
        sold: {alias: 'count'},
        genre: {alias: 'status'}
    };


    render() {
        const {context} = this.props;
        const authUser = context.authenticatedUser;

        if (!authUser) {
            return <Redirect to='/signin'/>
        }

        if (authUser && authUser.user_name) {
            return <Redirect to='/complain_dashboard'/>
        }

        return (
            <GridContent>
                <h3>Complaints Status</h3>
                <Row gutter={24}
                     type="flex"
                     style={{marginTop: 24,}}>
                    <Col xl={12} lg={24} md={24} sm={24} xs={24}>
                        <Suspense fallback={null}>
                            <Chart width={600} height={400} data={this.data} scale={this.cols}>
                                <Axis name="genre" title/>
                                <Axis name="sold" title/>
                                <Legend position="top" dy={-20}/>
                                <Tooltip/>
                                <Geom type="interval" position="genre*sold" color="genre"/>
                            </Chart>
                        </Suspense>
                    </Col>
                    <Col xl={12} lg={24} md={24} sm={24} xs={24}>
                        <Suspense fallback={null}>
                            <Pie/>
                        </Suspense>
                    </Col>
                </Row>
                <Container>
                    <Line />
                    <Divider />
                    <Container className={'mapChart'}>
                        <h3>Resource Surplus</h3>
                        <Liquid />
                    </Container>
                    <Divider />
                    <Container className={'mapChart'}>
                        <h3>Tubewells</h3>
                        <img src="https://gw.alipayobjects.com/zos/rmsportal/HBWnDEUXCnGnGrRfrpKa.png" alt="map"/>
                    </Container>

                </Container>

            </GridContent>

        )
    }
}
