import React from 'react';
import {Pie} from 'react-chartjs-2';

const data = {
    labels: [
        'Resolved',
        'Pending',
        'New',
    ],
    datasets: [{
        data: [3, 1, 2],
        backgroundColor: [
            '#36A2EB',
            '#4CC972',
            '#FFCE56',
        ],
        hoverBackgroundColor: [
            '#36A2EB',
            '#4CC972',
            '#FFCE56',
        ]
    }]
};

export default class MyPie extends React.Component {
    displayName =  'PieExample';

    render() {
        return (
            <div>
                <Pie data={data} />
            </div>
        );
    }
};
